                                    label="tagLabel" 
                                    track-by="tagName" 
                                    :close-on-select="false"
                                    :clear-on-select="false"
                                    :options="wikiTagOptions" 
                                    :multiple="true" 
                                    :taggable="true" 
                                    @remove="deleteAllTag"
                                    @tag="addContentTag"
                                >
